@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;800&family=Lato&family=Montserrat:wght@300;400;500;600;700;800&family=Play:wght@400;700&family=Playfair+Display+SC:wght@700&family=Poppins:wght@300;400;500;600;700&family=Varela+Round&display=swap');
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;

}

:root {
    --color-bg:#1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant:rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light:rgba(255,255,255,0.6);

    --transition: all 400ms ease ;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins' , sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg_texture.png);

}

/* GENERAL STYLES */

.container {
    width: var(--container-width-lg);
    margin:0 auto;
}
h1, h2, h3, h4, h5 {
    font-weight: 500;
}


h1 {
    font-size: 2.5rem;
}
h5 {
    font-size: 1rem;
}

section {
    margin-top: 8rem;
    height:125vh;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color :var(--color-light);
    
}

a{
    color: var(--color-primary);
    transition:var(--transition);
}

a:hover {
    color: var(--color-white);
}


.btn {
    width: max-content ;
    display: inline-block;
    color:var(--color-primary);
    padding:0.75rem 1.2rem;
    cursor:pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    border-radius: 4px;
}

.btn:hover {
    background: var(--color-white);
    color:var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color:var(--color-bg);
}
img {
    display:block;
    width: 100%;
    object-fit:cover;
}

/* media queries  */

@media screen and (max-width: 1024px) {
   .container  {width: var(--container-width-md);
   }

   section {
    margin-top: 6rem;
   }
}


@media screen and (max-width: 600px) {
    .container  {
    width: var(--container-width-md);
    }
    section{
        height: auto;
    }
 
    section > h2 {
     margin-bottom:2rem;
    }
 }







